import React from 'react'
import AppLayout from '@alicloud/console-components-app-layout'
import Page from '@alicloud/console-components-page'
import '@alicloud/console-components/dist/wind.css'
import styled from 'styled-components'
import TopBar, { TOP_BAR_HEIGHT } from './TopBar'
import SideBar from './SideBar'
import MarkdownContent from './MarkdownContent'
import { pageCtx } from './context'
import NotFound from './pages/404'
import IndexPage from './pages/indexPage'
import SEO from '../SEO'
import DynamicDoc from './DynamicDoc'
import DocPreview from './DocPreview'

export interface IDocPageMeta {
  // 404
  type: 'doc'
  /**
   *  kebab-case
   * https://lodash.com/docs/4.17.15#kebabCase
   *  frontmatter 
   */
  name: string
  /**
   * 
   *  frontmatter 
   */
  zhName: string
  /**
   * 
   *  frontmatter 
   */
  category: string
  /**
   * [sort, name]
   *  frontmatter (1)
   */
  sort: number | null
  /**
   * 
   */
  labelInMenu?: string
  mdxBody: string
  // url
  path: string
  // mdcontentRootDir
  markdownFilePath: string
  /**  */
  sideNav: {
    /**  */
    header: string
    /**  */
    navCategories: { categoryName: string }[]
  }
}

export interface IDynamicDocMeta {
  type: 'dynamic-doc'
  /**
   *  kebab-case
   * https://lodash.com/docs/4.17.15#kebabCase
   *  frontmatter 
   */
  name: string
  /**
   * 
   *  frontmatter 
   */
  zhName: string
  /**
   * 
   *  frontmatter 
   */
  category: string
  /**
   * 
   * https://www.unpkg.com/${packageName}@${packageVersion}/dist/_doc.system.js
   * 
   * https://cdn.jsdelivr.net/npm/${packageName}@${packageVersion}/dist/_doc.system.js
   */
  packageName: string
  packageVersion?: string
  /**
   * 
   */
  labelInMenu?: string
  // url
  path: string
  /**  */
  sideNav: {
    /**  */
    header: string
    /**  */
    navCategories: { categoryName: string }[]
  }
}

export type IPageMeta =
  | IDocPageMeta
  | IDynamicDocMeta
  | { type: '404' }
  | { type: 'index-page' }
  | { type: 'doc-preview' }

export interface ISiteMeta {
  categories: {
    name: string
    zhName: string
    docs: (IDocPageMeta | IDynamicDocMeta)[]
  }[]
  topNav: { text: string; href: string }[]
  // 
  primaryPath: string
  //  pageName  siteName
  siteName: string
  // 
  description?: string
}

export interface IPageContext {
  pageMeta: IPageMeta
  siteMeta: ISiteMeta
}

const SiteLayout: React.FC<{
  pageContext: IPageContext
}> = ({ pageContext }) => {
  // console.log('pageContext', pageContext)
  return (
    <pageCtx.Provider value={pageContext}>
      <SEO />
      <TopBar />
      <SAppLayout
        nav={'sideNav' in pageContext.pageMeta ? <SideBar /> : undefined}
        adjustHeight={TOP_BAR_HEIGHT}
        navCollapsible={false}
      >
        <Page>
          <Page.Content className="console-doc-content-body">
            {(() => {
              if (pageContext.pageMeta.type === 'doc') {
                return <MarkdownContent />
              }
              if (pageContext.pageMeta.type === 'dynamic-doc') {
                return <DynamicDoc />
              }
              if (pageContext.pageMeta.type === 'index-page') {
                return <IndexPage />
              }
              if (pageContext.pageMeta.type === 'doc-preview') {
                return <DocPreview />
              }
              return <NotFound />
            })()}
          </Page.Content>
        </Page>
      </SAppLayout>
    </pageCtx.Provider>
  )
}

export default SiteLayout

const SAppLayout = styled(AppLayout)`
  .windcc-app-layout__nav {
    ::-webkit-scrollbar {
      width: 5px;
    }

    ::-webkit-scrollbar-track {
      background: #dedede;
    }

    ::-webkit-scrollbar-thumb {
      background: #666;
    }
  }
`
